import BaseElement from './base-element';

export default class BaseDialogElement extends BaseElement{

    constructor(){
        super();
        this._cancel = this._cancel.bind(this);
    }

    // 
    _toggleStyle(shouldShow){
        this.style.display = shouldShow? 'block' : 'none';
    }

    _setVisible(shouldShow){
        this._toggleStyle(shouldShow)
    }

    _cancel() {
        console.log('cancel');
        this._setVisible(false)
    }

    show() {
        this._setVisible(true)
    }

    hide() {
        this._setVisible(false)
    }

    // life-cycle DOM
    connectedCallback(){
        console.log('connectedCallbacklife-cycle DOM');
        if(this._mask){
            this._mask.addEventListener('click', this._cancel)
        }
    }

    // life-cycle 
    attributeChangedCallback(){
        console.log('attributeChangedCallback, life-cycle ');
    }

    // life-cycle 
    disconnectedCallback(){
        console.log('attributeChangedCallback, life-cycle DOM');
    }

}